/* Global Styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background-color: #141414; /* Netflix-like dark background */
  color: #ffffff;
}

.App {
  text-align: center;
  padding: 20px;
}

h1 {
  font-size: 2.5rem;
  color: #e50914; /* Netflix red */
  margin-bottom: 30px;
}

/* Search Component */
.search-container {
  position: relative;
  width: 300px;
  margin: 0 auto;
}

input[type="text"] {
  padding: 10px;
  width: 100%;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  margin-bottom: 10px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  background-color: #e50914;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #f40612;
}

/* Dropdown */
.dropdown {
  position: absolute;
  top: 45px;
  width: 100%;
  background-color: #333;
  border: 1px solid #aca8a8;
  border-radius: 4px;
  max-height: 300px;
  overflow-y: auto;
  z-index: 10;
}

.dropdown li {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.dropdown li:hover {
  background-color: #444;
}

.dropdown-poster {
  width: 50px;
  height: 75px;
  margin-right: 10px;
  border-radius: 4px;
}

.dropdown span {
  font-size: 1rem;
}

/* Watchlist */
.Watchlist {
  margin-top: 30px;
}

.Watchlist h2 {
  color: #e50914;
  margin-bottom: 20px;
}

/*Open service button*/
.movie-link {
  display: flex;
  justify-content: center;
  padding: 10px;
  border-radius: 4px;
  background-color: #e50914;
  color: white;
  text-decoration: none;
}


/* Grid Layout */
.WatchlistGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  transition: 0.2s ease;
}

.Movie {
  background-color: #333;
  border-radius: 8px;
  padding: 15px;
  text-align: left;
  position: relative;
  overflow: hidden;
  transition: 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%; /* Ensure full height for consistent sizing */
}

.Movie:hover {
  transform: scale(1.01);
  transition: 0.2s ease-in-out;
}

.Movie img {
  width: 100%;
  border-radius: 4px;
  margin-bottom: 10px;
}

.Movie h3 {
  font-size: 1.2rem;
  margin: 10px 0;
}

/* Ensure select dropdown and textarea are the same height */
select {
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
  background-color: #3333339f;
  border: 0.25px solid #aca8a8;
  border-radius: 4px;
  color: white;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
  height: 48px; /* Match height of textarea */
}

select:focus {
  outline: none;
}

/* Remove Button */
.remove-button {
  position: absolute;
  top: 1px;
  right: 1px;
  background: #e509145b;
  border: none;
  color: white;
  font-size: 15px;
  width: 32px;
  height: 32px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: center;
  line-height: 10px;
}

.remove-button:hover {
  background-color: #f40612;
}

/* Progress Field */
.progress-container {
  margin-top: 10px;
}

textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #444;
  border-radius: 4px;
  background-color: #333;
  color: white;
  resize: vertical;
  height: 48px; /* Match height of select dropdown */
  box-sizing: border-box;
  margin-bottom: 10px;
}

/* Ensure button matches other elements in height */
button {
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  background-color: #e50914;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
  height: 48px; /* Ensure consistent height */
}

button:hover {
  background-color: #f40612;
}
